}

.login-input:focus {
  max-width: 100%;
  width: 500px;
}

.login-input:focus + .label-text {
  color: #f0f0f0;
  font-size: 13px;
  transform: translateY(-74px);
}

.login-input:valid + .label-text {
  font-size: 13px;
  transform: translateY(-74px);
}

.btnContainer {
  padding: 30px;
}

.btn {
  opacity: 1;
  outline: 0;
  color: #fff;
  font-size: 24px;
  line-height: 40px;
  position: relative;
  text-align: center;
  letter-spacing: 1px;
  display: inline-block;
  text-decoration: none;
  font-family: "Open Sans";
  text-transform: uppercase;
  cursor: pointer;
}

.btn::after {
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  color: #323237;
  display: block;
  transition: 0.5s;
  position: absolute;
  background: #adadaf;
  content: attr(data-back);
  transform: translateY(-50%) rotateX(90deg);
}

.btn::before {
  top: 0;
  left: 0;
  opacity: 1;
  color: #adadaf;
  display: block;
  padding: 0 30px;
  line-height: 40px;
  transition: 0.5s;
  position: relative;
  background: #323237;
  content: attr(data-front);
  transform: translateY(0) rotateX(0);
}

.btn:hover::after {
  opacity: 1;
  transform: translateY(0) rotateX(0);
}

.btn:hover::before {
  opacity: 0;
  transform: translateY(50%) rotateX(90deg);
}

