.nav-btn {
  border: none;
  background-color: transparent;
  color: grey;
  width: 140px;
  height: 40px;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  position: relative;
  outline: none;
  border: 3px solid grey;
  border-radius: 3px;
  transition: color 0.5s, transform 0.2s, background-color 0.2s;
}

.nav-btn:active {
  transform: translateY(3px);
}

.nav-btn:hover {
  background-color: transparent;
  box-shadow: none;
  color: white;
}

.nav-btn::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  width: 100%;
  height: 100%;

  transition: opacity 0.3s, border 0.3s;
}

.nav-btn:hover::before {
  opacity: 0;
}

.nav-btn::after {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 3px solid white;
  opacity: 0;
  z-index: 1;
  transform: scaleX(1.2) scaleY(1.4);
  transition: transform 0.3s, opacity 0.3s;
}
.nav-btn:hover::after {
  opacity: 1;
  transform: scaleX(1) scaleY(1);
}

.nav-btn.opened {
  color: white;
}

.nav-btn.opened::before {
