@import url(https://fonts.googleapis.com/css?family=Montserrat);

.black-background {
  background-color: rgba(0, 0, 0, 0.795);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.settings {
  background-color: #333333;
  width: 70%;
  height: 60%;
  margin: 0 auto;
  margin-top: 150px;
}
.settings-header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 10%;
}

.settings-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 120px;
  height: 15%;
  border-bottom: 3px solid rgba(255, 255, 255, 0.623);
}

.close-settings-btn {
  font-size: 40px;
  color: rgb(138, 19, 19);
  transition: all 0.2s ease-in-out;
}
.close-settings-btn:hover {
  color: red;
}
.settings-container {
  background-color: grey;
  height: 75%;
}

.tab-btn {
  border: none;
  background-color: transparent;
  color: grey;
  width: 140px;
  height: 40px;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  position: relative;
  outline: none;
  border: 3px solid grey;
  border-radius: 3px;
  transition: color 0.5s, transform 0.2s, background-color 0.2s;
}

.tab-btn:active {
  transform: translateY(3px);
}

.tab-btn::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  width: 100%;
  height: 100%;

  transition: opacity 0.3s, border 0.3s;
}
.tab-btn::after {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 3px solid white;
  opacity: 0;
  z-index: 1;
  transform: scaleX(1.2) scaleY(1.4);
  transition: transform 0.3s, opacity 0.3s;
}

.tab-btn:hover {
  background-color: transparent;
  box-shadow: none;
  color: white;
}

.tab-btn.activated::before {
  border: 3px solid white;
}
.tab-btn:hover::after {
  opacity: 1;
  transform: scaleX(1) scaleY(1);
}

.activated {
  color: white;
}

.tabs-container {
  height: 100%;
}

.tabs-container2 {
  width: 66.6%;
  height: 80%;
}

.settings-options-container {
  width: 33.3%;
  height: 80%;
}

.selectors {
  background-color: khaki;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.selector-btn {
  width: 160px;
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.option {
  width: 33.3%;
  height: 80%;
}
